const globalOptions = {
	alpha: .4,
	cssID: 'dummy-font',
	type: 'JUST_ONE_LINE',
	speed: [4,15],
	origin: { x: 'centerX', y: 'centerY'},
	offset:{x:0, y:0},
	size: { w: document.body.offsetWidth, h: window.innerHeight },
}

export default globalOptions


